<template>
  <transition name="slide">
    <div class="wrapper">
      <div class="header">
        <i class="back" @click="back"></i>
        <span class="text">交易记录</span>
      </div>
      <div class="content-wrapper">
        <scroll class="content">
          <ul class="list">
            <li class="item" v-for="(item,index) in list">
              <div class="order">
                <label>{{item.creationTime|formatTime}}</label>
                <span>{{item.code}}</span>
              </div>
              <div class="money">
                <div class="reduce">
                  <label>扣除补贴：</label>
                  <span>{{item.subsidyMoney|formatMoney}}</span>
                </div>
                <div class="num">
                  {{item.money|formatMoney}}
                </div>
              </div>
            </li>
          </ul>
        </scroll>
      </div>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import Scroll from 'base/scroll/scroll'
  import {ERR_CODE} from 'api/config'
  import {getMemberOrder} from 'api/features'

  export default {
    data () {
      return {
        list: []
      }
    },
    filters:{
      formatTime(val){
        if(!val){
          return ''
        }
        return `${val.substring(0,10)} ${val.substring(11, 19)}`
      }
    },
    created () {
      this._getMemberOrder()
    },
    methods: {
      back() {
        this.$router.back()
      },
      // 交易记录
      _getMemberOrder() {
        getMemberOrder().then((res) => {
          if(res.errCode === ERR_CODE){
            this.list = res.data
          }
        })
      }
    },
    components: {
      Scroll
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .slide-enter,.slide-leave-to
    transform translate3d(100%,0,0)

  .slide-enter-active,.slide-leave-active
    transition all 0.3s

  .wrapper
    position absolute
    top 0
    left 0
    right 0
    bottom 0
    background $color-background
    z-index 1
    .header
      position fixed
      height 44px
      top 0
      left 0
      right 0
      background $color-header-background
      display flex
      justify-content center
      align-items center
      .back
        position absolute
        background url("~common/image/back.png") no-repeat
        background-size contain
        width 8px
        height 15px
        left 15px
        top 15px
        &:before
          position absolute
          left -10px
          right -10px
          top -10px
          bottom -10px
          content ''
      .text
        color $color-header-text
        font-size $font-size-large
        font-weight bold
    .content-wrapper
      position fixed
      left 0
      right 0
      top 44px
      bottom 0
      .content
        width 100%
        height 100%
        overflow hidden
        margin-top 10px
        .list
          background #fff
          .item
            display flex
            flex-direction column
            justify-content center
            height 64px
            border-bottom 1px solid #E6E6E6
            box-sizing border-box
            margin 0 14px
            &:last-child
              border-bottom none
            .order
              display flex
              justify-content space-between
              font-size $font-size-small
              color #999
            .money
              display flex
              margin-top 16px
              justify-content space-between
              font-size $font-size-medium-m
              display flex
              color #333
              .reduce label,.reduce span,.num
                font-weight bold

</style>



